import React, { useState, useEffect } from 'react';
import logo from './icons/logo.png';
import useClasses from './style';
import Modal from '../../components/Modal';
import firstTimer from './icons/firstTimer.png'
import {
  Box,
  Button,
  Typography,
} from '@material-ui/core'
import { useHistory, useParams } from 'react-router-dom';
import auth from 'API-Service/auth';
import { useToasts } from 'react-toast-notifications';

function MailVerification() {
  const classes = useClasses();
  const params = useParams();
  const { addToast } = useToasts();
  const [verified, setVerified] = useState(false);
  const [error, setError] = useState(false);
  const history = useHistory()

  useEffect(() => {
    if (params.token) {
      auth
      .verify(params.token)
      .then(response => {
        console.log(response)
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 2000, autoDismiss: true })
          setVerified(true);
          setTimeout(function(){history.push('/login');}, 2000);
        } else {
          setError(true);
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      })
    }
  }, [params])
  return (
    <Box className={classes.root} style={{height: "auto"}}>
      <Box className={classes.card}>
        <Box 
          component="img"
          alt="logo"
          src={logo}
          style={{marginBottom: "1rem"}}
        />
        <Typography className={classes.headline} >
          {verified ? "We have verified you!" : error ? "The token is expired or invalid!" : "We're verifying you!"}
        </Typography>
      </Box>
    </Box>
  )
}

export default MailVerification